import { createAPI, metricsOptions } from '~/ui/utils/api';

const methods = {
    async installMetric(metricsData: metricsOptions['body']) {
        this.metric({
            body: {
                name: metricsData.name,
                data: metricsData.data,
            },
            method: 'POST',
        });
    },
};
export const metricsInstall = createAPI<any, typeof methods>({
    name: 'metrics:install',
    endpoint: 'metric/',
    methods,
    read: () => {},
});
